
body{
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding-bottom: 1000px;
    overflow-x: hidden;
    padding-right:15px;
}

.options{
    background-color: rgb(255, 247, 237);
    border: 1px solid rgb(255, 247, 237);

}
.chartpage{
  display: flex;
  justify-content: space-between;

}
.links {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.5s ease-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  z-index: 1; /* Add this line */
}
.c-top-section {
  position: absolute;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 900px;
  background-color: #f24952;
}

.top-section {
  position: absolute;
  width: 100vw; /* set the width to be the full width of the viewport */
  height: 900px;
  background-color: #8383ff;
}
.top-section-content{
  display: flex;
  justify-content: space-between;
  padding: 2%;
  margin-top: 15%;
}
.c-top-section-content{
  display: flex;
  justify-content: space-between;
  padding: 2%;
  margin-top: 15%;
}
.links.scrolled {
  background-color: white;
  width : 100vw;
  left: 0%;
}

h1 {
  font-size: 3.5rem;
  font-weight: 400;
  color: black;
  margin-bottom: -2%;
}
.subtitle {
  width: 90%;
  font-weight: 200;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  color: black;
}

.cta-button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  background-color: transparent;
  padding: 0.5rem 1rem;
  color: black;
  border-radius: 0;
  border-color: black;
}
button:focus,
button:active {
  outline: none;
  box-shadow: none;
}
.top-left {
font-family: Arial, Helvetica, sans-serif;
  position: absolute;
  top: 6%;
  left: 2%;

}
.top-right {
  position: absolute;
  top: 6%;
  right: 2%;
}
.chart-button{
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    color: black;
    border:none;
}
.chart-button:hover{
  color: rgba(38, 0, 255, 1);
}

.stats-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    color: black;
    border:none;
}
.stats-button:hover{
  color: rgba(38, 0, 255, 1);
}
.api-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    color: black;
    border:none;
}
.api-button:hover{
  color: rgba(38, 0, 255, 1);
}
.home-button{
    font-size: 1rem;
    background-color: transparent;
    padding: 0.5rem 1rem;
    color: black;
    border:none;
}
.home-button:hover{
  color: rgba(38, 0, 255, 1);
}
.player-stat-row{
  min-height: 3rem;
}
.chart-container{
  position: absolute;
  top: 150%;
  width: 100%;
  padding: 1%;
}
.chart-header{
  position: relative;
  left: 1%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 2rem;
  color: black;
}
.player-search-title{
  position: relative;
  font-weight: 400;
  font-size: 1rem;
  color: black;
  margin-bottom: 2%;
}
.player-search{
  margin-top: 10px;
  border:rgb(255, 247, 237);
}
.player-search:focus{
  outline: none;
  box-shadow: none;
}
.player-stat-row{
  margin-top: 2%;
  margin-bottom: 2%;
  border: 1px solid rgb(255, 247, 237);
  border-radius: 0;
  padding: 1%;
}
.delete-player{

  text-align: center;
  color: red;
  border-radius: 0;
  border : none;
}
.total-point-stat{
  font-size: 0.8rem;
  color: black;
  padding-top: 3px;
}

/* remove default border and box shadow */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  border-color: black;
  box-shadow: none;
}

/* apply custom border */
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  border-radius: 0px;
  border: 1px solid black;
}

/* remove focus highlight */
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-select-single.ant-select-show-arrow:not(.ant-select-customize-input):hover .ant-select-selector {
  color: inherit;
  border-color: black;
  box-shadow: none;
}
.my-slider .ant-slider-handle {
  border-color: brown !important;
  background-color: rgb(15, 15, 15) !important;
}

.my-slider .ant-slider-handle:focus {
  border-color: brown !important;
  background-color: rgb(15, 15, 15) !important;
}

.my-slider .ant-slider-handle-dragging {
  border-color: brown !important;
  background-color: chartreuse !important;
  outline: none;
}


.back-to-top{
  color: black;
  border-radius: 0;
  left: 2%;
}
.chart-buttons{
  border : 1px solid black;
  background-color: white;
  border-radius: 0%;
  margin-right: 1%;
  margin-top: 1%;
  
}
footer{
  position: absolute;
  bottom: -100%;
  background-color: white;
  color: black;
  padding: 1%;

}
.hp-image{
  width: 100%;
  height: auto;
}
.cp-image{
  margin-top: -20px;
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 750px) {
  /* CSS rules for phones */
  .top-section {
    height: 1200px;
  }
  .c-top-section {
    height: 1200px;
  }
  .top-section-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1%;
    margin-top: 20%;
  }
  .top-section-content > div {
    justify-content: left;
    width: 100%;
    margin-bottom: 400px;
  }
  .c-top-section-content > div {
    justify-content: left;
    width: 100%;
    margin-bottom: 400px;
  }

  .hp-image{
    margin-top:-300px;
    position: relative;
    width: 90vw;
    height: auto;
    margin-bottom: 20px;
  }
  .cp-image{
    margin-top:250px;
    position: relative;
    width: 90vw;
    height: auto;
    margin-bottom: 20px;
    left: -92%;

  }
  .subtitle{
    padding: 0 5%;
    width: 90vw;
  }
  h1{
    padding: 0 5%;
    width: 90vw;
  }
  .chart-container{
    position: absolute;
    top: 200%;
    width: 100%;
    padding: 1%;
  }
}
